<template>
	<div id="order-note">
		<public-header title="添加备注"></public-header>
		<div class="content">
			<van-cell-group class="text">
				<van-field v-model="note" type="textarea" placeholder="请输入备注，最多50字哦"/>
			</van-cell-group>
			<div class="save-btn">
				<van-button type="danger" size="large" @click="onSubmit">保存</van-button>
			</div>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				note: '',
				showPreLoading: true,
				title: '添加备注',
				query: {}
			}
		},
		methods: {
			...mapMutations([
				'setState'
			]),
			onLoad() {
				this.note = this.erranderStoreExtra.note;
				this.showPreLoading = false;
			},
			onSubmit() {
				this.setState({type: 'erranderStoreExtra', key: 'note', val: this.note});
				this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/store/order/diy', query: this.query}));
			}
		},
		computed: {
			...mapState([
				'erranderStoreExtra'
			]),
		},
		mounted() {
			this.query = this.util.parseQuery(this.$route.query);
			this.onLoad();
		}
	}
</script>

<style>
	#order-note .content .text .van-field{
		padding: 15px;
	}
	#order-note .content textarea{
		background-color: #f5f5f5;
		height: 140px;
		border:1px solid #ECECEC;
		padding:10px;
		font-size: 16px;
	}
	#order-note .content .label-box{
		padding: 10px 15px 5px 15px;
	}
	#order-note .content .label-box .label{
		display: inline-block;
		padding: 4px 10px;
		font-size:12px;
		border:1px solid #ECECEC;
		margin: 0 10px 10px 0;
		color: #333;
		line-height: 20px
	}
	#order-note .content .save-btn{
		padding: 15px;
	}
	#order-note .add-invoice{
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 15px;
		color: #ff2d4b;
		background-color: #fff;
		font-size: 14px;
	}
	#order-note .add-invoice .icon{
		font-size: 18px;
		padding-right: 5px;
	}
</style>


